/* eslint-disable react/prefer-stateless-function */
import React, { useEffect, useContext } from 'react';
import { OverlayProvider } from '@react-aria/overlays';

// Data layer imports
import {
    SocialFeedProvider,
    SocialFeedContext,
} from 'PlugAndPlay/_data-layer/jsx/global';
import { SocialFeedService } from 'PlugAndPlay/_data-layer/js/global';

import { createCard } from './card-types/CardFactory';
import '../css/_global.scss'; // Last so our CSS overrides any dependencies above

export default function SocialFeed({
    title,
    numberOfCards,
    socialFeedAdapter,
}) {
    const socialService = new SocialFeedService({ socialFeedAdapter });

    // Wrap the app in its Context Provider so the content data can be accessed within
    return (
        <div className="social-feed-wrapper">
            <SocialFeedProvider socialFeedService={socialService}>
                <SocialFeedContent
                    title={title}
                    numberOfCards={numberOfCards}
                />
            </SocialFeedProvider>
        </div>
    );
}

/**
 * SocialFeed application
 * @returns SocialFeed object
 */
function SocialFeedContent({ title, numberOfCards }) {
    const { feedData, getFeedData } = useContext(SocialFeedContext);

    // Initial data load
    useEffect(() => {
        getFeedData({
            count: numberOfCards,
        });
    }, [getFeedData]);

    return (
        <OverlayProvider className="social-feed-wrapper__overlay">
            <div className="no-wysiwyg social-feed">
                <div className="social-feed__header">{title}</div>
                <div className="social-feed__body">
                    {feedData.map((cardDetails) => {
                        return createCard(cardDetails);
                    })}
                </div>
            </div>
        </OverlayProvider>
    );
}
